*{
  color: white;
  background-color: rgba(17, 0, 56, 0.945);
  font-family: Helvetica, Arial, sans-serif;
}
body{
  display: flex;

  flex-wrap: wrap;
  justify-content: center;
  text-align: center;
}
input{
  width: 100%;
  font-size: 1.2rem;
  color: whitesmoke;
}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;

}
.card-grid h3{
  width: 100%;
}


.card{
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  padding: 5px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}
.card p:hover{
  background-color: black;
}

.card p{
  padding: 5px;
  text-align: center;

}

.card img{
  max-height: 170px;
}